<template>
	<div class="loginRole">
		<web-view :src="renzhen"></web-view>
	</div>
</template>

<script>
	import {pordRenZhen} from "@/utils/request/config.js";
	export default {
		components: {

		},
		data() {
			return {
				renzhen: ""
			};
		},
		methods: { 
		},
		onLoad(options) {
			let authorizations = null
			let urls =""
			if (uni.getStorageSync("storage_token") != 'undefined' && uni.getStorageSync("storage_token") != null && uni
				.getStorageSync("storage_token") != "") {
				authorizations = JSON.parse(uni.getStorageSync("storage_token")).token
			}
			if(	uni.getStorageSync("role-user")==1){
				// 个人
				urls="/certification/uploadDocumentsIndividual?token=" + authorizations
			}else{
				 
				urls="/certification/uploadDocuments?token=" + authorizations+"&userType=enterprise"+"&id="+JSON.parse(uni.getStorageSync("storage_token")).user.id
			}
			 console.log('kkkkkkkk',pordRenZhen+urls)
			this.renzhen = pordRenZhen+urls 
		}
	};  
	// https://dev-pub-mob.lvxiaogou.com/certification/uploadDocumentsIndividual 
	// https://dev-pub-mob.lvxiaogou.com/certification/uploadDocuments
</script>

